<script async type="text/javascript"
        src="/static/js/jquery.countdown.min.js"></script>
<script>
import {
  MULTIPLEWHEEL_MODULE,
  RESET_WHEEL_SPIN,
  WHEEL_CHANCES,
  WHEEL_GET_MY_LOGS,
  WHEEL_LOGS,
  WHEEL_MODULE,
  WHEEL_SPIN
} from "@/store/wheel.module";
import {errorCodeHelper, locale, uiHelper} from "@/util";
import {SHARED} from "@/constants/constants";
import {MEMBER_DETAIL} from "@/store/member.module";
import $ from "jquery";
import currencySymbolMapp from "currency-symbol-map";

export default {
  name: "WheelOfFortune",
  data: () => ({
    isLoggedIn: uiHelper.isAuthenticated(),
    btnNames: ['funwheel', 'happywheel', 'crazywheel'],
    wheelName: 'Fun Wheel',
    selectedwheel: null,
    activeBtn: 'funwheel',
    normalClass: '',
    activeClass: 'enlarge',
    backgroundColor: '',
    is_spinning: false,
    wheel_id: 0,
    currencySymbolMapp: currencySymbolMapp,
    language: uiHelper.getLanguage(),
    currentCurrency: uiHelper.getCurrency(),
    winList: true,
    recordList: false,
    prizeList: [],
    win_amount: 0,
    reward_id: 0,
    degree: 0,
    arrDegree: [0, -45, -90, -135, 180, 135, 90, 45],
    stopDegree: 0,
    isWinPrize: true,
    error: ''
  }),
  created() {
    this.metaTag()
    this.selectedwheel = {
      id: 0,
      name: '',
      start_date: '',
      end_date: '',
      event_setting: {
        event_id: 0,
        coin_required: 0,
        no_of_rewards: 0,
        main_image_url: '',
        frame_image_url: '',
        start_button_image_url: '',
        home_gif_url: ''
      },
      event_rewards: []
    }
    this.getMemberDetails();
    this.getMultipleWheel();
  },
  computed: {
    currencySymbol() {
      // Map the currency code to a symbol using a map or other lookup
      return currencySymbolMapp(this.currentCurrency);
    },
    wheelInfo() {
      // const wheel = this.$store.state.wheel.multipleWheel.find(wheel => wheel.name === this.wheelName);
      // this.selectedwheel = wheel
      // this.activeBtn = btnName;
      // const wheel = this.$store.state.wheel.multipleWheel.find(wheel => wheel.name === btnName);
      return this.$store.state.wheel.multipleWheel;
    },
    memberInfo() {
      return this.$store.state.member.info
    },
    chances() {
      return this.$store.state.wheel.wheelChances.chances
    },
    wheelLogs() {
      return this.$store.state.wheel.wheelLogs
    },
    wheelMyLogs() {
      return this.$store.state.wheel.wheelMyLogs
    },
    spinResult() {
      return this.$store.state.wheel.wheelSpin.complete
    }
  },
  watch: {
    memberInfo() {
      return this.$store.state.member.info
    },
    isLoggedIn() {
      if (this.$store.state.member.isLoggedIn) {
        this.getWheel()
        this.getMultipleWheel()
        this.getMemberDetails()
      }
    },
    spinResult() {
      if (this.$store.state.wheel.wheelSpin.complete) {
        if (this.$store.state.wheel.wheelSpin.success) {
          this.win_amount = this.$store.state.wheel.wheelSpin.win_amount
          this.reward_id = this.$store.state.wheel.wheelSpin.reward_id
          let get_index = this.prizeList.indexOf(this.reward_id);
          this.degree = this.arrDegree[get_index]
          this.stopDegree = this.degree + (360 * 2)
          this.rotate();
        } else {
          this.error = this.$store.state.wheel.wheelSpin.error
          this.openWinPopup()
        }
      }
    },
    wheelInfo() {
      this.prizeList = [];
      let k = this.$store.state.wheel.multipleWheel[1];
      this.selectedwheel = k
      this.wheel_id = k.id
      this.prizeList = k.event_rewards.map(x => x.id);
      this.getWheelLogs(k)
      this.getWheelChances(k)
      this.getWheelMyLogs(k)
    },
    wheelLogs() {
      return this.$store.state.wheel.wheelLogs
    },
    wheelMyLogs() {
      return this.$store.state.wheel.wheelMyLogs
    },
    chances() {
      return this.$store.state.wheel.wheelChances.chances
    }
  },
  methods: {
    metaTag() {
      const currentUrl = window.location.href;
      uiHelper.setCanonical(currentUrl)
      const domainWithProtocol = currentUrl.split('/')[2];
      const domain = domainWithProtocol.replace(/(^\w+:|^)\/\//, '');
      const path = window.location.pathname;
      const cleanPath = path.replace(/\/(en-BD|bn-BD)/i, '');
      const Links = `https://${domain}${cleanPath}`;
      const Linksen = `https://${domain}/en-BD${cleanPath}`;
      const Linksbn = `https://${domain}/bn-BD${cleanPath}`;
      uiHelper.setMetaTag('url', Links)
      uiHelper.setMetaTag('og:url', Links)
      uiHelper.setHreflangLinks(Links)
      uiHelper.setHreflangLinksen(Linksen)
      uiHelper.setHreflangLinksbd(Linksbn)
    },
    selectBtn(btnName) {
      this.activeBtn = btnName;
      switch (btnName) {
        case 'funwheel':
          this.wheelName = 'Fun Wheel';
          break;
        case 'happywheel':
          this.wheelName = 'Happy Wheel';
          break;
        case 'crazywheel':
          this.wheelName = 'Crazy Wheel';
          break;
        default:
          this.wheelName = 'Wheel';
          break;
      }
      this.selectedwheel = this.$store.state.wheel.multipleWheel.find(wheel => wheel.name === this.wheelName);
      this.prizeList = this.selectedwheel.event_rewards.map(x => x.id);
      this.getWheelLogs(this.selectedwheel)
    },
    showNextWheel() {
      const currentIndex = this.btnNames.indexOf(this.activeBtn);
      const nextIndex = (currentIndex + 1) % this.btnNames.length;
      this.activeBtn = this.btnNames[nextIndex];
      switch (this.activeBtn) {
        case 'funwheel':
          this.wheelName = 'Fun Wheel';
          break;
        case 'happywheel':
          this.wheelName = 'Happy Wheel';
          break;
        case 'crazywheel':
          this.wheelName = 'Crazy Wheel';
          break;
        default:
          this.wheelName = 'Wheel';
          break;
      }

      this.selectedwheel = this.$store.state.wheel.multipleWheel.find(wheel => wheel.name === this.wheelName);
      this.prizeList = this.selectedwheel.event_rewards.map(x => x.id);
      this.getWheelLogs(this.selectedwheel)
    },
    showPreviousWheel() {
      const currentIndex = this.btnNames.indexOf(this.activeBtn);
      const previousIndex = (currentIndex - 1 + this.btnNames.length) % this.btnNames.length;
      this.activeBtn = this.btnNames[previousIndex];
      switch (this.activeBtn) {
        case 'funwheel':
          this.wheelName = 'Fun Wheel';
          break;
        case 'happywheel':
          this.wheelName = 'Happy Wheel';
          break;
        case 'crazywheel':
          this.wheelName = 'Crazy Wheel';
          break;
        default:
          this.wheelName = 'Wheel';
          break;
      }

      this.selectedwheel = this.$store.state.wheel.multipleWheel.find(wheel => wheel.name === this.wheelName);
      this.prizeList = this.selectedwheel.event_rewards.map(x => x.id);
      this.getWheelLogs(this.selectedwheel)
    },
    getMemberDetails() {
      this.$store.dispatch(`${MEMBER_DETAIL}`)
    },
    closeWinDialog() {
      this.$parent.closePageDialog();
      this.resetWheel();
      this.is_spinning = false
    },
    openWinPopup() {
      let dialog = this.$parent.initializePageDialogProperty()
      dialog.title = locale.getMessage(`label.wofv3`)

      if (this.error != "") {
        dialog.message.push(this.error)

        dialog.button.push({
          title: locale.getMessage(`label.close`),
          action: () => {
            this.closeWinDialog()
          }
        })
      } else if (this.win_amount == 0) {
        dialog.message.push(locale.getMessage(`message.congratsZero`, [this.$options.filters.currency(this.win_amount, "BDT")]))
        dialog.button.push({
          title: locale.getMessage(`label.ok`),
          action: () => {
            this.closeWinDialog()
          }
        })
      } else {
        dialog.message.push(locale.getMessage(`message.congrats`, [this.$options.filters.currency(this.win_amount, "BDT")]))
        dialog.button.push({
          title: locale.getMessage(`label.ok`),
          action: () => {
            this.closeWinDialog()
          }
        })
      }
      dialog.dialogXButton = null
      this.$parent.openPageDialog(dialog)
    },
    rotate() {
      $('.rouletteMain_box_part').css('transition', 'all 0.5s ease;');
      $('.rouletteMain_box_part').css('transform', 'rotate(' + this.degree + 'deg)');
      let func = this;
      var timer = setTimeout(function () {
        if (func.degree >= func.stopDegree) {

          window.clearTimeout(timer);
          setTimeout(function () {
            func.openWinPopup();
          }, 1000)
        } else {
          func.degree += 5;
          window.requestAnimationFrame(() => {
            func.rotate()
          });
        }

      }, 1)
    },
    showWinnerList() {
      this.winList = true;
      this.recordList = false;
    },
    showRecordList() {
      this.recordList = true;
      this.winList = false;
    },
    resetWheel() {
      let filter = {
        id: this.selectedwheel.id
      }
      this.$store.dispatch(`${RESET_WHEEL_SPIN}`, {filter})

      if (this.isLoggedIn) {
        this.$store.dispatch(`${WHEEL_CHANCES}`, {filter})
      }

      this.getMemberDetails()
      let reset = 0
      $('.rouletteMain_box_part').css('-webkit-transform', 'rotate(' + reset + 'deg)');
    },
    getWheelLogs(k) {
      let filter = {
        id: k.id
      }
      this.$store.dispatch(`${WHEEL_LOGS}`, {filter})
    },
    getWheelChances(k) {
      if (!this.isLoggedIn) return;

      let filter = {
        id: k.id
      }
      this.$store.dispatch(`${WHEEL_CHANCES}`, {filter})
    },
    getWheelMyLogs(k) {
      if (!this.isLoggedIn) return;

      let filter = {
        id: k.id
      }
      this.$store.dispatch(`${WHEEL_GET_MY_LOGS}`, {filter})
    },
    getWheel() {
      let currency = uiHelper.getCurrency();
      let filter = {
        currency: currency
      }
      this.$store.dispatch(`${WHEEL_MODULE}`, {filter})
    },
    getMultipleWheel() {
      let currency = uiHelper.getCurrency();
      let filter = {
        currency: currency
      }
      this.$store.dispatch(`${MULTIPLEWHEEL_MODULE}`, {filter})
    },
    spinWheel() {
      if (!this.is_spinning) {
        this.is_spinning = true
        let filter = {
          id: this.selectedwheel.id
        }
        this.$store.dispatch(`${WHEEL_SPIN}`, {filter})
      }
    }
  },
  async mounted() {
    let func = this;
    let degree = 270;
    let arrDegree = [0, -45, -90, -135, 180, 135, 90, 45]
    let stopDegree = (270) + (360 * 6);

    /**
     * Created by Administrator on 2017/7/5.
     * 红包雨
     */
    function rotate() {
      // $('.rouletteMain_box_part').css('-webkit-transform', 'rotate(' + degree + 'deg)');
      // var timer = setTimeout(function () {
      //   if (degree >= stopDegree) {
      //     window.clearTimeout(timer);
      //   } else {
      //     degree += 5;
      //     rotate();
      //   }
      //
      // }, 1)
    }

    $(document).ready(function () {
      $('.rouletteMain_box_btn').on("click", function () {
        if (!func.isLoggedIn) {
          func.$parent.openLoginDialog()
          return;
        }
        func.spinWheel();
      })
    });
  }
}
</script>

