.rouletteMain, .rouletteMain:after, .rouletteMain:before, .rouletteMain_awards_item.special .rouletteMain_awards_medals, .rouletteMain_awards_item > div, .rouletteMain_box_wrap:before, .rouletteMain_winner_wrap:after, .rouletteMain_winner_wrap:before, .rouletteMain_wrap:after, .rouletteMain_wrap:before {
  background-size: 100% 100%
}

.rouletteMain, .rouletteMain_awards_item, .rouletteMain_box, .rouletteMain_winner, .rouletteMain_winner_wrap:after, .rouletteMain_winner_wrap:before {
  height: 0
}

.rouletteMain_winner_wrap:after, .rouletteMain_winner_wrap:before {
  content: "";
  position: absolute
}

.rouletteMain:after, .rouletteMain:before, .rouletteMain_awards_item > div, .rouletteMain_box_coinWrap, .rouletteMain_box_numberTimes > div, .rouletteMain_box_wrap, .rouletteMain_box_wrap:after, .rouletteMain_box_wrap:before, .rouletteMain_winner_wrap, .rouletteMain_wrap {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%
}

.roulette {
  /*font-family: Arial, Helvetica, Microsoft YaHei, Microsoft JhengHei, PingFangTC, sans-serif;*/
  overflow: hidden;
  width: 100%;

}

.funwheel.roulette {
  background-image: url(https://jiliwin.9terawolf.com/images/krikya/nspin/Wealth_Desktop.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.happywheel.roulette {
  background-image: url(https://jiliwin.9terawolf.com/images/krikya/nspin/Prosperity_Desktop.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.crazywheel.roulette {
  background-image: url(https://jiliwin.9terawolf.com/images/krikya/nspin/Fortune_Desktop.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.roulette * {
  box-sizing: border-box
}

.rouletteMain {

  margin: 0 auto;
  max-width: 1440px;
  padding-bottom: 57.6388888889%;
  position: relative;
  width: 100%
}

.rouletteMain:after, .rouletteMain:before {
  animation: rouletteDecoShake 25s infinite;
  content: ""
}

.rouletteMain:before {
  animation-direction: alternate;
  animation-timing-function: linear;
  background-image: url(https://csi.20icipp.com/img/static/roulette/roulette_balloon.png)
}

.rouletteMain:after {
  animation-direction: alternate-reverse;
  background-image: url(https://csi.20icipp.com/img/static/roulette/roulette_ball.png)
}

.rouletteMain_wrap:after, .rouletteMain_wrap:before {
  /*background-image: url(https://s3.ap-southeast-1.amazonaws.com/media-dev.ezgame4u.com/cms/babu/image/6225b4f336723.png);*/
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  transform: rotateY(180deg);
  width: 100%;
}

.rouletteMain_wrap:before {
  left: -100%
}

.rouletteMain_wrap:after {
  left: 100%
}

.rouletteMain_head {
  display: flex;
  justify-content: center;
  margin-bottom: 3.4722222222%;
  padding: 6px 0;
  position: relative;
  z-index: 1
}

.rouletteMain_item + .rouletteMain_item {
  margin-left: 36px
}

.rouletteMain_item > a {
  color: #fff;
  display: block;
  font-size: 20px;
  line-height: 48px;
  padding: 0 3px;
  position: relative;
  text-decoration: none
}

.rouletteMain_item > a:after {
  background-color: #ffdb00;
  border-radius: 5px;
  bottom: 0;
  content: "";
  height: 5px;
  left: 0;
  position: absolute;
  transition: width .3s;
  width: 0
}

.rouletteMain_item > a.active:after, .rouletteMain_item > a:hover:after {
  width: 100%
}

