.rouletteMain_box_part_list {
  align-items: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  height: 50%;
  justify-content: center;
  padding-bottom: 2%;
  position: absolute;
  top: 0;
  transform-origin: 50% 100%
}

.rouletteMain_box_part_list:before {
  animation-direction: alternate;
  animation-duration: .5s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  background-image: linear-gradient(0deg, #ffcf00, #ff0 14%, #ff0 35%, #fff 42%, #ffc600 72%);
  content: "";
  height: 100%;
  left: 50%;
  margin-left: -1px;
  position: absolute;
  top: 0;
  transform-origin: 50% 100%;
  transition: width .3s, margin-left .3s;
  width: 2px;
  z-index: 1
}

.rouletteMain_box_part_list:after {
  font-family: Verdana, sans-serif;
  font-size: 4.5833333333vw;
  font-weight: 700;
  position: relative;
  text-shadow: -.2083333333vw -.2777777778vw 0 #dd3f07, -.1388888889vw -.2777777778vw 0 #dd3f07, -.0694444444vw -.2777777778vw 0 #dd3f07, 0 -.2777777778vw 0 #dd3f07, .0694444444vw -.2777777778vw 0 #dd3f07, .1388888889vw -.2777777778vw 0 #dd3f07, .2083333333vw -.2777777778vw 0 #dd3f07, .2777777778vw -.2777777778vw 0 #dd3f07, .2777777778vw -.2083333333vw 0 #dd3f07, .2777777778vw -.1388888889vw 0 #dd3f07, .2777777778vw -.0694444444vw 0 #dd3f07, .2777777778vw 0 0 #dd3f07, .2777777778vw .0694444444vw 0 #dd3f07, .2777777778vw .1388888889vw 0 #dd3f07, .2777777778vw .2083333333vw 0 #dd3f07, .2777777778vw .2777777778vw 0 #dd3f07, .2083333333vw .2777777778vw 0 #dd3f07, .1388888889vw .2777777778vw 0 #dd3f07, .0694444444vw .2777777778vw 0 #dd3f07, 0 .2777777778vw 0 #dd3f07, -.0694444444vw .2777777778vw 0 #dd3f07, -.1388888889vw .2777777778vw 0 #dd3f07, -.2083333333vw .2777777778vw 0 #dd3f07, -.2777777778vw .2777777778vw 0 #dd3f07, -.2777777778vw .2083333333vw 0 #dd3f07, -.2777777778vw .1388888889vw 0 #dd3f07, -.2777777778vw .0694444444vw 0 #dd3f07, -.2777777778vw 0 0 #dd3f07, -.2777777778vw -.0694444444vw 0 #dd3f07, -.2777777778vw -.1388888889vw 0 #dd3f07, -.2777777778vw -.2083333333vw 0 #dd3f07, -.2777777778vw -.2777777778vw 0 #dd3f07
